import React, { useEffect, useState } from 'react';

const Success = () => {
  const [verifiedItems, setVerifiedItems] = useState([
    { text: 'Email', verified: false },
    { text: 'Authorization', verified: false },
    { text: 'Violation Check', verified: false },
    { text: 'Details Submitted', verified: false }
  ]);

  useEffect(() => {
    const verificationTimer = setTimeout(() => {
      setVerifiedItems(prev => 
        prev.map((item, index) => 
          !item.verified && index === prev.findIndex(i => !i.verified) 
            ? { ...item, verified: true } 
            : item
        )
      );
    }, 600);

    return () => clearTimeout(verificationTimer);
  }, [verifiedItems]);

  return (
    <div className="success-screen">
      <div className="checkmark-animation">
        <svg viewBox="0 0 52 52">
          <circle 
            className="checkmark-circle"
            cx="26" 
            cy="26" 
            r="25" 
            fill="none"
          />
          <path
            className="checkmark-check"
            fill="none"
            d="M14.1 27.2l7.1 7.2 16.7-16.8"
          />
        </svg>
      </div>
      
      <h1 className="success-heading">
        Appeal Complete!
      </h1>
      
      <p className="success-text">
        Thank you for submitting your appeal.
      </p>

      <div className="verification-list">
        {verifiedItems.map((item, index) => (
          <div 
            key={index} 
            className={`verification-item ${item.verified ? 'verified' : ''}`}
            style={{ transitionDelay: `${index * 0.1}s` }}
          >
            <span className="verification-text">{item.text}</span>
            <span className="verification-status">
              {item.verified ? '-Passed' : '-Processing...'}
            </span>
          </div>
        ))}
      </div>
      
      <div className="redirect-message">
        Returning to Your Account
        <div className="progress-line" />
      </div>

      <style jsx>{`
        .success-screen {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          min-height: 100vh;
          padding: 2rem;
          text-align: center;
        }
        
        .checkmark-animation {
          width: 80px;
          height: 80px;
          margin-bottom: 2rem;
        }
        
        .checkmark-animation svg {
          width: 100%;
          height: 100%;
        }
        
        .checkmark-circle {
          stroke: #42b72a;
          stroke-width: 2;
          stroke-dasharray: 166;
          stroke-dashoffset: 166;
          animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
        }
        
        .checkmark-check {
          stroke: #42b72a;
          stroke-width: 2;
          stroke-dasharray: 48;
          stroke-dashoffset: 48;
          animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
        }
        
        .success-heading {
          font-size: 1.8rem;
          font-weight: 600;
          color: #1c1e21;
          margin: 0 0 1rem 0;
          line-height: 1.3;
        }
        
        .success-text {
          font-size: 1.1rem;
          color: #65676b;
          margin: 0 0 2rem 0;
          line-height: 1.6;
        }
        
        .verification-list {
          display: flex;
          flex-direction: column;
          gap: 0.75rem;
          margin-bottom: 2.5rem;
          width: 100%;
          max-width: 400px;
        }
        
        .verification-item {
          display: flex;
          justify-content: space-between;
          opacity: 0.6;
          transform: translateY(10px);
          transition: all 0.4s ease-out;
        }
        
        .verification-item.verified {
          opacity: 1;
          transform: translateY(0);
        }
        
        .verification-text {
          font-weight: 500;
          color: #1c1e21;
        }
        
        .verification-status {
          color: #42b72a;
          font-weight: 500;
        }
        
        .redirect-message {
          font-size: 1rem;
          color: #65676b;
          margin-top: 1.5rem;
          position: relative;
          padding-bottom: 0.5rem;
        }
        
        .progress-line {
          height: 2px;
          width: 0;
          background: #42b72a;
          position: absolute;
          bottom: 0;
          left: 0;
          animation: progress 2s linear forwards;
        }
        
        @keyframes stroke {
          100% {
            stroke-dashoffset: 0;
          }
        }
        
        @keyframes progress {
          0% { width: 0; }
          100% { width: 100%; }
        }
      `}</style>
    </div>
  );
};

export default Success;